
.cell-media-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 18px;
  position: relative;
}
.cell-media-wrap > a {
    text-decoration: none;
}
.cell-media-wrap:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1px;
    background: #eee;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.cell-media-wrap .cell-media-top {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.cell-media-wrap .cell-media-top .cell-media {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      padding-right: 45px;
}
.cell-media-wrap .cell-media-top .cell-title {
      font-size: 16px;
      line-height: 24px;
      color: #333;
      font-weight: bold;
      text-align: left;
}
.cell-media-wrap .cell-media-top .cell-detail {
      font-size: 12px;
      padding-top: 12px;
      height: 4em;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #939393;
      text-align: left;
}
.cell-media-wrap .cell-media-top .pull-right {
      width: 94px;
      height: 94px;
      overflow: hidden;
      background-position: center center;
      background-size: cover;
}
.cell-media-wrap .cell-media-top .pull-right img {
        width: 100%;
}
.cell-media-wrap .cell-media-bottom {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding-top: 20px;
    margin-top: 12px;
    color: #bfbfbf;
    position: relative;
}
.cell-media-wrap .cell-media-bottom:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 1px;
      background: #eee;
}



