
.cell {
  position: relative;
  padding: 10px 5px 10px 15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.cell:before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    height: 1px;
    background: #eee;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.cell > .cell-title {
    font-size: 15px;
}
.cell img {
    width: 25px;
    height: 25px;
    position: relative;
    bottom: -2px;
}



