
header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 48px;
  padding: 0 10px;
  background: #fff;
  color: #42bd56;
}
header > .header-button {
    width: 80px;
    -webkit-box-align: stretch;
        -ms-flex-align: stretch;
            align-items: stretch;
}
header > .header-button.on-right {
      text-align: right;
}
header > .header-button.on-left {
      text-align: left;
}
header > .header-button a {
      text-decoration: none;
      color: #42bd56;
}
header > .header-title {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    font-size: 16px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
header.is-bg {
    background: #494949;
    color: #fff;
}
header.is-bg a {
      color: #fff;
}
header.is-bg .header-title {
      color: #fff;
}
header.is-fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 8;
}



